@font-face {
  font-family: "futura";
  src: url("../fonts/futura-bt-for-dyson-w05-medium.woff");
  font-weight: normal;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*:active,
*:focus {
  outline: none;
}

html {
  min-height: 100vh;
  overflow-x: hidden;
}

body {
  font-family: "futura";
  font-style: normal;
  font-weight: 400;
  color: #000000;
  background-color: #FFFFFF;
  overflow-x: hidden;
}
body.modal_open {
  height: 100vh;
  overflow: hidden;
}

h1, h2, h3, h4 {
  margin: 0;
}

a, input {
  font-family: "futura", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
  color: inherit;
  border: none;
}

a:hover {
  color: #bd016f;
}

input:focus {
  outline: none;
}

button {
  padding: 0;
  background: none;
  border: none;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

